/* ---------------------------- 
        01 := google fonts
---------------------------- */
@font-face {
    font-family: 'bebas_neue_probold';
    src: url('./assets/fonts/bebas-neue-pro-bold-webfont.eot');
    src: url('./assets/fonts/bebas-neue-pro-bold-webfont.eot?#iefix') format('embedded-opentype'),
         url('./assets/fonts/bebas-neue-pro-bold-webfont.woff2') format('woff2'),
         url('./assets/fonts/bebas-neue-pro-bold-webfont.woff') format('woff'),
         url('./assets/fonts/bebas-neue-pro-bold-webfont.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;

}


/* ---------------------------- 
        02 := variables
---------------------------- */
:root{
    --background_bleu1 : #4c5981;
    --background_bleu2 : #838eae;
    --background_jaune : #ffe45c;
    --ecriture_jaune : #d7b600;
}


/* ---------------------------- 
        03 := global
---------------------------- */
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    overflow-x: hidden;
    font-family: 'bebas_neue_probold';
}

ul{
    list-style-type: none;
}

button{
    width: max-content;
    padding: 1rem 2rem;
    margin: auto;
    border: 3px solid black;
    border-radius: 1rem;
    font-size: 2rem;
    font-weight: bold;
    background-color: white;
    cursor: pointer;
}

.row{
    display: flex;
    flex-direction: row;
}

.column{
    display: flex;
    flex-direction: column;
}

.uppercase{
    text-transform: uppercase;
}

.capitalize{
    text-transform: capitalize;
}

.white{
    color: white;
}

.black{
    color: black;
}

