       ********** Table of Contents **********

13. =section1 : liens
14. =section2 : contact
15. =responsive

-----------------------------------------------*/



/* -------------------------------------
        13 := section1 : liens
------------------------------------- */
footer{
    background-color: var(--background_bleu1);
    color: white;  
    font-weight: bold;
    font-size: 1.8rem;
    padding-bottom: 6rem;
}

footer section{
    padding: 3rem 5rem 4rem 5rem;
    justify-content: space-between;
}

footer ul:first-child{
    order: 1;
}

footer ul + ul{
    order: 2;
}

footer form{
    order: 3;
}

footer section ul, footer form{
    width: 30%;
}

footer section li{
    margin-top: .2rem;
}

footer input{
    width: 90%;
    padding: .8rem 1rem;
    margin-top: .8rem;
}

footer input::placeholder{
    color: var(--background_bleu2);
    font-weight: bold;
    font-size: 1.1rem;
}



/* -------------------------------------
        14 := section2 : contact
------------------------------------- */
footer section + ul{
    padding-left: 4.5rem;
}

footer section + ul li{
    border: 2px solid white;
    border-radius: 50%;
    padding: 1.2rem 1.2rem .5rem 1.2rem;
    margin: 0 .5rem;
    font-size: 3rem;
}




/* --------------------------
        15 := responsive
-------------------------- */
@media screen and (min-width:768px) and (max-width:1000px){
    footer{
        font-size: 1.6rem;    
    }

    footer section{
        padding: 3rem 3.5rem 4rem 3.5rem;
    }

    footer section ul, footer form{
        width: 31%;
    }

    footer section + ul{
        padding-left: 2.5rem;
    }

    footer section + ul li{
        font-size: 2rem;
    }
}

@media screen and (min-width:576px) and (max-width:769px) {
    footer{
        font-size: 1.4rem;
    }

    footer section{
        padding: 3rem 1.5rem 4rem 1.5rem;
    }

    footer section ul, footer form{
        width: 36%;
    }

    footer section ul:first-child{
        width: 20%;
    }

    footer input{
        width: 98%;
    }

    footer input::placeholder{
        font-size: 1rem;
    }

    footer section + ul{
        padding-left: 2.5rem;
    }

    footer section + ul li{
        padding: 1rem 1rem .5rem 1rem;
        margin: 0 .5rem;
        font-size: 1.6rem;
    }
}

@media screen and (max-width:575px){
    footer{
        font-size: 1.4rem;
        padding-bottom: 4rem;
    }

    footer section{
        flex-wrap: wrap;
        padding: 2rem 1.5rem 2rem 1.5rem;
    }

    footer ul:first-child{
        order: 3;
        width: 30%;
    }
    
    footer ul + ul{
        order: 2;
        width: 60%;
    }

    footer ul{
        margin-top: 1.5rem;
    }
    
    footer form{
        order: 1;
        width: 80%;
        margin: auto;
    }

    footer section + ul{
        padding-left: 2rem;
    }

    footer section + ul li{
        padding: .8rem .8rem .5rem .8rem;
        font-size: 1.2rem;
    }
}