/* -------------------------------------
        04 := section1 : navigation
------------------------------------- */
header{
    background-image: url(../../assets/Arri√®re\ plan.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    align-content: center;
}

.navigation{
    justify-content: space-between;
}

.logo{
    margin-left: 4%;
    margin-top: 1.5rem;
    width: 30%;
}

nav ul{
    padding-right: 1.5rem;
    padding-top: 2.5rem;
    font-size: 1.4rem;
}

nav ul li{
    padding-right: 2rem;
}

nav ul li:nth-child(4){
    padding-right: .5rem;
}



/* -------------------------------------
        05 := section2 : photos
------------------------------------- */
header .sect2{
    width: 100%;
    margin-top: 2.2rem;
    justify-content: space-between;
    padding-bottom: 5rem;
}

header .papillon{
    height: 13rem;
    width: 21%;
    align-self: flex-end;
}

header .tournesol{
    height: 26rem;
    align-self: flex-start;
}

header .album{
    width: 65%;
    text-align: center;
}

header .album h1{
    font-size: 3rem;
    margin-top: 6rem;
    margin-bottom: 3rem;
}

header .album img{
    width: 50%;
    margin: auto;
}

header .album button{
    margin-top: 3rem;
    margin-bottom: 4rem;
}

.modal{
    background-color: white;
}


/* --------------------------
        15 := responsive
-------------------------- */
@media screen and (max-width:576px){
    body{
        overflow-x: hidden;
    }
}

@media screen and (min-width:576px) and (max-width:769px) {
    .logo{
        margin-top: 1.2rem;
        width: 25%;
    }
    
    nav ul{
        padding-top: 2rem;
        font-size: 1.1rem;
    }

    header .sect2{
        margin-top: 0;
    }

    header .papillon{
        height: 9rem;
        width: 22%;
    }

    header .tournesol{
        height: 15rem;
    }

    header .album h1{
        font-size: 2.5rem;
        margin-top: 4.2rem;
        margin-bottom: 2.8rem;
    }
    
    header .album img{
        width: 67%;
    }

    header .album button{
        font-size: 1.5rem;
    }
}

@media screen and (min-width:768px) and (max-width:1000px){
    header .papillon{
        height: 10rem;
    }

    header .album img{
        width: 65%;
    }

    header .album button{
        font-size: 1.5rem;
    }
}