/*-----------------------------------------------
    
       ********** Table of Contents **********

07. =section1 
08. =section2 
09. =section3
10. =section4
11. =section5
12. =responsive

-----------------------------------------------*/


/* ---------------------------- 
        07 := section 1
---------------------------- */
.section1{
    margin: 4rem 0 6rem;
    justify-content: center;
}

.section1 .alice-carousel__next-btn [data-area]::after,
.section1 .alice-carousel__prev-btn [data-area]::after {
    font-size: 7rem;
    position: absolute;
    top: 30%;
}

.section1 .alice-carousel__prev-btn [data-area]::after{
    left: 1.1%;
}

.section1 .alice-carousel__next-btn [data-area]::after{
    right: 1.1%;
}

.section1 div{
    justify-content: center;
}

.section1 .alice-carousel__dots{
    display: none;
}

.section1 button{
    padding: 1rem 2.5rem;
}

.section1 article{
    width: 46%;
    justify-content: center;
    padding-left: 2rem;
}

.section1 img{
    width: 100%;
}

.section1 article + article{
    padding: 2.8rem 0 0 4rem;
    margin-left: 2rem;
}

.section1 article + article img{
    width: 90%;
}

.section1 p{
    margin: .4rem 0 .2rem .2rem;
}

.section1 span{
    margin-left: .2rem;
    color: var(--ecriture_jaune);
}

.section1 p, .section1 span{
    font-size: 2.4rem;
}





/* ---------------------------- 
        08 := section 2
---------------------------- */
.section2, .section3{
    width: 85%;
}

.section2{
    background-color: var(--background_bleu1);
}

.section2 img{
    height: 18rem;
    margin-top: -6rem;
}

.section2 iframe{
    width: 84%;
    margin: -8rem 8% 3rem 8%;
    height: 28rem;
}




/* ---------------------------- 
        09 := section 3
---------------------------- */
.section3{
    background-color: var(--background_jaune);
    margin-left: 15%;
    margin-top: 6rem;
    padding-top: 6rem;
}

.section3 h2{
    color: var(--background_bleu1);
    font-size: 12rem;
    margin-left: -7.1rem;
    margin-top: -11.8rem;
    position: absolute;
}

table{
    font-size: 1.5rem;
    width: 100%;
    padding-left: 2%;
    text-align: center;
}

table td{
    height: 4.5rem;
}

table button{
    font-size: 1.5rem;
    padding: .5rem 3.5rem;
}

table .nom{
    color: var(--background_bleu1);
}

table .date{
    background-color: white;
    width: max-content;
    padding: .2rem .5rem;
}





/* ---------------------------- 
        10 := section 4
---------------------------- */
.section4{
    margin: 4rem 0;
    text-align: center;
    color: var(--background_bleu2);
    font-weight: bold;
}

.section4 h2, .section5 h2{
    color: var(--background_bleu1);
    font-size: 4rem;
    margin-bottom: 5rem;
}

.section4 div{
    width: 100%;
    padding: 0 6rem;
    justify-content: space-between;
}

.section4 article{
    width: 26%;
}

.section4 figure{
    background-color: var(--background_bleu2);
}

.section4 article:nth-child(2) figure{
    background-color: var(--background_jaune);
}

.section4 img{
    width: 200%;
    margin-left: -54%;
    height: 20rem;
    margin-top: -3rem;
}

.section4 article:nth-child(2) img{
    width: 210%;
}

.section4 article:last-child img{
    width: 150%;
    margin-left: -24%;
}

.section4 p{
    margin: .5rem 0 .3rem;
}

.section4 p, .section4 span{
    font-size: 1.6rem;
}





/* ---------------------------- 
        11 := section 5
---------------------------- */
.section5 h2{
    margin-left: 4%;
    margin-bottom: 1.5rem;
}

.section5 div{
    justify-content: center;
}

.section5 article{
    width: 32%; 
}

.section5 img{
    width: 95%;
    height: 14rem;
    border-radius: 1rem;
}

.section5 p{
    color: var(--background_bleu1);
    font-size: 2rem;
    margin: 1rem 1rem 0 2rem;
}


/* ---------------------------- 
        12 := responsive
---------------------------- */
@media screen and (max-width:575px){
    body{
        overflow-x: hidden;
    }
    
    .section1 .alice-carousel__next-btn [data-area]::after,
    .section1 .alice-carousel__prev-btn [data-area]::after {
        font-size: 3rem;
    }

    .section1 article{
        width: 90%;
    }

    .section1 article + article{
        padding: 0;
        margin-left: 0;
    }
}

@media screen and (min-width:576px) and (max-width:769px) {
    .section1 .alice-carousel__next-btn [data-area]::after,
    .section1 .alice-carousel__prev-btn [data-area]::after {
        font-size: 5rem;
        top: 12%;
    }

    .section1 img{
        width: 90%;
        margin: auto;
    }

    .section1 article + article{
        margin-left: 1.2rem;
    }

    .section1 article + article img{
        width: 80%;
    }

    .section1 p, .section1 span{
        font-size: 1.8rem;
    }

    .section1 button{
        font-size: 1.5rem;
    }

    .section2 iframe{
        margin-top: -9rem;
        height: 18rem;
    }

    .section3 h2{
        color: var(--background_bleu1);
        font-size: 9rem;
        margin-left: -5.3rem;
        margin-top: -10rem;
    }

    .section4 h2, .section5 h2{
        font-size: 3rem;
        margin-bottom: 4rem;
    }

    .section4 div{
        padding: 0 2rem;
    }

    .section4 article{
        width: 30%;
    }

    .section4 img{
        height: 15rem;
    }

    .section4 p, .section4 span{
        font-size: 1.3rem;
    }

    .section5 h2{
        margin-bottom: 1.5rem;
    }

    .section5 img{
        height: 10rem;
    }

    .section5 p{
        font-size: 1.1rem;
        margin: .5rem .5rem 0 1rem;
    }
}

@media screen and (min-width : 768px) and (max-width: 895px) {
    .section1 .alice-carousel__next-btn [data-area]::after,
    .section1 .alice-carousel__prev-btn [data-area]::after {
        font-size: 5.2rem;
        top: 15%;
    }
}

@media screen and (min-width:768px) and (max-width:1000px) {
    .section1 .alice-carousel__next-btn [data-area]::after,
    .section1 .alice-carousel__prev-btn [data-area]::after {
        font-size: 5.5rem;
        top: 22%;
    }

    .section2 iframe{
        height: 20rem;
    }

    .section3 h2{
        font-size: 11rem;
        margin-left: -6.4rem;
    }

    .section4 div{
        padding: 0 3rem;
    }

    .section4 article{
        width: 28%;
    }

    .section4 img{
        height: 15rem;
        margin-top: -1.5rem;
    }

    .section4 p, .section4 span{
        font-size: 1.1rem;
    }

    .section5 h2{
        margin-left: 6%;
    }

    .section5 .chevron{
        margin-top: 4rem;
    }

    .section5 img{
        height: 11rem;
    }

    .section5 p{
        font-size: 1.5rem;
    }
}